
import { Message } from 'element-ui';
import routerApi from '@/network/baseRouter';


export function Tip(Obj, flage = true) {

    if(Obj === undefined) {
        return Message({
            showClose: true,
            message: '',
            type: 'error'
        })
    }
    let msg;
    let status = Obj.status;                   //      
    let url = Obj.config.url;               //  
    let type = 'success';                    // 
    // get 
    let length = url.indexOf('?');
    url = (length == -1) ? url : url.substring(0, length);  // ?

    switch (status) {
        case 200:
            msg = '';
            break;
        case 201:
            msg = '';
            break;
        case 202:
            msg = '';
            break;
        case 204:
            msg = '';
            break;
        case 400:
            msg = '';
            break;
        case 401:
            msg = '';
            break;
        case 403:
            msg = ',';
            break;
        case 404:
            msg = '';
            break;
        case 500:
            msg = 'bug';
            break;
        case 501:
            msg = '';
            break;
        case 502:
            msg = '';
            break;
        default:
            msg = '';
    }

    switch (url) {

        /**API */
        case routerApi.articleServilce.add:

            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';
            Message({
                showClose: true,
                message: msg,
                type: type
            });

            break;
        case routerApi.articleServilce.find:

            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            // 
            if (!flage) {
                Message({
                    showClose: true,
                    message: msg,
                    type: type
                });
            }

            break;
        case routerApi.articleServilce.findAll:

            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            // 
            if (!flage) {
                Message({
                    showClose: true,
                    message: msg,
                    type: type
                });
            }

            break;
        case routerApi.articleServilce.findLike:

            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            // 
            if (!flage) {
                Message({
                    showClose: true,
                    message: msg,
                    type: type
                });
            }

            break;
        case routerApi.articleServilce.findNear:

            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            // 
            if (!flage) {
                Message({
                    showClose: true,
                    message: msg,
                    type: type
                });
            }

            break;

        case routerApi.articleServilce.findLikeSum:
            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            // 
            if (!flage) {
                Message({
                    showClose: true,
                    message: msg,
                    type: type
                });
            }
            break;    
        case routerApi.articleServilce.findSome:

            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            // 
            if (!flage) {
                Message({
                    showClose: true,
                    message: msg,
                    type: type
                });
            }

            break;
        case routerApi.articleServilce.findSum:
            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            // 
            if (!flage) {
                Message({
                    showClose: true,
                    message: msg,
                    type: type
                });
            }
            break;
        case routerApi.articleServilce.update:
            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            Message({
                showClose: true,
                message: msg,
                type: type
            });

            break;
        case routerApi.articleServilce.updateRead:
            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            // 
            if (!flage) {
                Message({
                    showClose: true,
                    message: msg,
                    type: type
                });
            }
            break;
        case routerApi.articleServilce.delete:
            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            Message({
                showClose: true,
                message: msg,
                type: type
            });
            break;

        /**API */
        case routerApi.directoryService.add:

            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';
            Message({
                showClose: true,
                message: msg,
                type: type
            });

            break;
        case routerApi.directoryService.findAll:
            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            // 
            if (!flage) {
                Message({
                    showClose: true,
                    message: msg,
                    type: type
                });
            }
            break;
        case routerApi.directoryService.update:
            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            Message({
                showClose: true,
                message: msg,
                type: type
            });

            break;
        case routerApi.directoryService.delete:
            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            Message({
                showClose: true,
                message: msg,
                type: type
            });
            break;

        /**API */
        case routerApi.userService.add:

            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';
            Message({
                showClose: true,
                message: msg,
                type: type
            });

            break;
        case routerApi.userService.find:

            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';

            // 
            if (!flage) {
                Message({
                    showClose: true,
                    message: msg,
                    type: type
                });
            }

            break;
        
        /**API */
        case routerApi.files.uploadImage:
            msg = '';
            break;
        case routerApi.files.uploadFile:
            msg = '';
            break;

        /**API */
        case routerApi.calenderService.commit:
            
            msg = flage ? '' : '' + msg;

            type = flage ? 'success' : 'error';
            Message({
                showClose: true,
                message: msg,
                type: type
            });

            break;

        case routerApi.calenderService.getCalender:
             msg = '';
             break;
            
        default:
            msg = '' + url;
            type = "error";
            Message({
                showClose: true,
                message: msg,
                type: type
            });
    }

}