import instance from './base.js'

/**
 * 
 * @param {*} url 
 * @param {*} msg 
 * {
 * id:Number                // ID,
 * date:String              // ,
 * allFinish:Number         // ,
 * gridData:Array           // 
 * }
 */
export function http_post_commitCalenderPlan(url,msg){
    return instance.post(url,msg);
}

/**
 * 
 * @param {*} url 
 * @param {*} date 
 * date:String              // 
 */
export function http_get_getCalenderPlan(url,date){
    return instance.get(`${url}?date=${date}`);
}